// --- COMPONENTS ---

function SimpleStat({ label, value }: { label: string; value: string }) {
  return (
    <div className="px-6 lg:px-12 flex flex-col items-center lg:items-start gap-1">
      <span className="text-xs font-bold uppercase tracking-widest text-muted-foreground">
        {label}
      </span>
      <span className="text-xl lg:text-2xl font-bold text-foreground">
        {value}
      </span>
    </div>
  );
}

function SectionLabel({ number, title }: { number: string; title: string }) {
  return (
    <div className="flex items-center gap-4 mb-12">
      <span className="w-12 h-12 rounded-full border border-border flex items-center justify-center text-sm font-mono font-bold text-muted-foreground">
        {number}
      </span>
      <h2 className="text-3xl md:text-4xl font-bold tracking-tight text-foreground">
        {title}
      </h2>
    </div>
  );
}

function CurriculumTabs({ curriculum }: { curriculum: any[] }) {
  const [activeTab, setActiveTab] = useState(0);

  useEffect(() => {
    setActiveTab(0);
  }, [curriculum]);

  if (!curriculum?.length) return null;
  const currentYear = curriculum[activeTab] || curriculum[0];

  return (
    <div className="bg-card rounded-[2.5rem] border border-border overflow-hidden">
      <div className="flex overflow-x-auto no-scrollbar border-b border-border bg-muted/30 p-2 gap-2">
        {curriculum.map((year, idx) => (
          <button
            key={idx}
            onClick={() => setActiveTab(idx)}
            className={cn(
              "flex-shrink-0 px-8 py-4 rounded-2xl text-sm font-bold uppercase tracking-wider transition-all duration-300 border border-transparent",
              activeTab === idx
                ? "bg-background text-primary shadow-sm border-border"
                : "text-muted-foreground hover:bg-background/50 hover:text-foreground"
            )}
          >
            {year.year}
          </button>
        ))}
      </div>

      <div className="p-8 md:p-12 relative min-h-[400px]">
        <AnimatePresence mode="wait">
          <motion.div
            key={activeTab}
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            transition={{ duration: 0.3 }}
          >
            <div className="mb-10">
              <span className="text-primary font-bold text-sm uppercase tracking-widest mb-2 block">
                Focus Area
              </span>
              <h3 className="text-4xl font-bold">{currentYear.focus}</h3>
            </div>

            <div className="grid md:grid-cols-2 gap-4">
              {currentYear.subjects?.map((subject: string, idx: number) => (
                <div
                  key={idx}
                  className="flex items-center gap-4 p-5 rounded-2xl bg-muted/30 border border-border/50 hover:border-primary/50 transition-colors"
                >
                  <span className="text-xs font-bold text-muted-foreground/50">
                    {(idx + 1).toString().padStart(2, "0")}
                  </span>
                  <span className="font-bold text-foreground">{subject}</span>
                </div>
              ))}
            </div>
          </motion.div>
        </AnimatePresence>
      </div>
    </div>
  );
}
